import { createContext, useContext, useState, useEffect } from "react";
import Axios from "axios";
import apiUrl from "../constants/index"

import dynamic from 'next/dynamic';

const Identity = createContext([{active: null, users:[]}, undefined]);




export class IdentityProvider extends React.Component{
    constructor(props) {
        super(props);

        this.state= {
            id: {active: null, users: []},
            axios: undefined
        };

        this.beforeMount();
    }

    request = (req) => {
        const ide = this.state.id;
        if(Number.isInteger(ide.active) && ide.users && ide.users[ide.active]) {
            console.log("token supplied");
            req.headers.Authorization = `Bearer ${ide.users[ide.active].accessToken}`;
            return req;
        }
        throw "Request not authorized";
    }

    response = (res) => {
        if(res.status === 401) {
            var no = res.headers["Www-Authenticate"].split(",");
            ///...

            ///removeUser(id.users[id.active].idString);
        } 
        return res;
    }

    reject = (error) => {
        // Do something with request error
        return Promise.reject(error);
    }

    addUser = (idString, username, accessToken) => {
        this.setState ((prev) => {
            var newO = {...prev.id};

            var index = newO.users.findIndex((val) => val.username === username);
            if(index===-1)
                newO.users = [...newO.users, {idString, username, accessToken}];
            else {
                var e = [...newO.users];
                e[index].accessToken = accessToken;
                delete e[index].error;
                newO.users = e;
            }
            
            return {id: newO};
        });
    }

    removeUser= (username) => {
        this.setState((prev) => {
            var newO = {...prev.id};

            var index = newO.users.findIndex((val) => val.username === username);

            if(index>-1)
                newO.users.splice(index, 1);
            
            return {id: newO};
        });
    }

    setActive = (idx) => {
        this.setState((prev) => {
            var newO = {...prev.id};

            if(idx > -1 && idx < prev.id.users.length || idx === null) {
                newO.active = idx;    
            } 
            return {id: newO};
        }, this.checkActive);
    }

    persist() {
        console.log("persist");
        localStorage.setItem("active", JSON.stringify(this.state.id.active));
        localStorage.setItem("users", JSON.stringify(this.state.id.users));
    }

    checkActive() {
        if(this.state.id.active !== null) {
            if(this.state.id.users[this.state.id.active].test === true) {
                return;
            }

            this.state.axios.get("/protected")
            .then((val) => {
                console.log("✔ Account ok: ",val);
            }, (reason)=>{
                console.log("❌ Account bad: ", reason);

                if(reason.response && reason.response.headers["www-authenticate"]) {
                    //authenticate header
                    var auth = reason.response.headers["www-authenticate"];
                    console.log(auth);
                    var tokens = auth.split(",").map(x => x.trim());

                    var error= {};

                    //parse header
                    if(tokens[1] && tokens[1].startsWith('error="')) {
                        error.err = tokens[1].substring(7, tokens[1].length-1);
                    }
                    if(tokens[2] && tokens[2].startsWith('error_description="')) {
                        error.desc = tokens[2].substring(19, tokens[2].length-1);    
                    }

                    //decide what to do
                    switch(error.err) {
                        case "UnsupportedAuthError":
                            throw reason;
                        
                        case "TokenClaimError":
                            if(error.desc === "insufficient claims.") {
                                error.icon = "bolt"
                            } else if(error.desc === "invalid claims.") {
                                error.icon = "death"
                            }
                            break;

                        case "TokenExpiredError":
                            error.icon = "time";
                            break;

                        case "NotBeforeError":
                            error.icon = "time";
                            break;

                        case "JsonWebTokenError":
                            error.icon = "bolt";
                            break;
                    }

                    console.log(error);
                    if( error.icon ) {
                        return this.setState((prev) => {
                            var newUsers = [...prev.id.users];
                            
                            newUsers[prev.id.active] = {...newUsers[prev.id.active], error: error};
    
                            return {id:{...prev.id, users: newUsers, active: null}};
                        });
                    }
                } 

                //tf happened
                this.setState((prev) => {return {id:{...prev.id, active: null}}});
                
            })
        }
    }

    beforeMount() {
        if(typeof window === 'undefined') {
            return;
        }
        console.log("before");

        //get initial data
        var active,users;

        try {
            active = JSON.parse(localStorage.getItem("active") || "null");  
        } catch (error) {
            active= this.state.id.active;
        }
        try {
            users = JSON.parse(localStorage.getItem("users") || "[]");
        } catch (error) {
            users= this.state.id.users;
        }
        

        const instance = Axios.create({
            baseURL: "https://drkslv.herokuapp.com"
        });

        instance.interceptors.request.use(this.request, this.reject);
        instance.interceptors.response.use(this.response, this.reject);

        //pass context down
         
        this.state.id= {active, users, addUser: this.addUser, removeUser: this.removeUser, setActive: this.setActive};
        this.state.axios = instance;
        //this.setState(prev=> {return {id: {active, users, addUser: this.addUser, removeUser: this.removeUser, setActive: this.setActive}, axios: instance}});
    }

    componentDidMount() {
        publish();
        this.checkActive();
    }


    render() {
        if(typeof window !== 'undefined') {
            this.persist();
        }
        return <Identity.Provider value={[this.state.id, this.state.axios]}>
            {this.props.children}
        </Identity.Provider>;
    }
        
}
    

export const useIdentity = () => {
    return useContext(Identity);
}

